import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'

export default {
	data() {
		return {
			avatar: [
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
			]
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName', 'avatarUrl', 'gender']),
	methods: {
		...mapActions(['login']),
		toMain(userName) {
			this.login(userName);
			/**
			 * 强制登录时使用reLaunch方式跳转过来
			 * 返回首页也使用reLaunch方式
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '/pages/tabBar/home/home',
				});
			} else {
				uni.navigateBack();
			}
		},
		login_weixin() {
			//微信登录
			let self = this;
			uni.login({
				provider: 'weixin',
				success(loginRes) {
					console.log(loginRes.authResult);

					//Get user info
					uni.getUserInfo({
						provider: 'weixin',
						success(infoRes) {
							console.log('User Info: %s', JSON.stringify(infoRes.userInfo));

						 	self.toMain(infoRes.userInfo);
						},
						fail() {
							uni.showToast({
								icon: 'none',
								position: 'bottom',
								title: '登陆失败'
							});

						}
					})
				},
				fail(err) {
					console.error('授权登陆失败： %s', JSON.stringify(err))
				}
			})
		}
	},
	onLoad() {
		
	}
}
